<template>
  <div id="work">
    <div class="inner">
      <div class="work_top_content">
        <div class="work_text_box js-motion">
          <h2 class="work_title" ref="title">
            참여했던 프로젝트를<br />
            소개합니다.
          </h2>
          <h3 class="work_subtitle js-motion" ref="subTitle">
          <span class="count" ref="count">0</span>개의 프로젝트를 성공적으로 해냈습니다.
          </h3>
        </div>
      </div>
      <CardList />
    </div>
  </div>
</template>
    
<script>
import { ref, onMounted } from 'vue';
import { gsap, Expo, Power2, Linear } from 'gsap';
import { ScrollTrigger } from 'gsap/ScrollTrigger';
import CardList from '@/components/Layouts/List/CardList.vue'
gsap.registerPlugin(ScrollTrigger);


export default {
  name: 'WorkList',
  props: {
  },
  components: {
    CardList,
  },
  setup() {
    const title = ref(null);
    const subTitle = ref(null);
    const count = ref( 0 );
    const game = {score:0};
    
    const titleShow =  () =>{
      
      const items = gsap.utils.toArray(".js-motion");
      gsap.set(  items, { opacity: 0, y:50 });
        gsap.to(  items, 0.75, { opacity: 1, y:0, ease:Expo.easeInOut, onComplete: () => {
          gsap.to( game, 4, {score:"+=58", roundProps:"score", ease:Linear.easeNone, onUpdate:upDateHandler});
      }});

      items.forEach( (item) =>{
        let tl = gsap.timeline({
            scrollTrigger: {
                trigger: item,
                start: "top  center",
                // markers:true,
                scrub: true,

            }
        });
        tl.set(item, { autoAlpha: 1  });
        tl.to(item, 0.75, {
            autoAlpha:0,
            ease: Power2.out
        });
      });

    
    }

    const upDateHandler = () => {
      count.value.innerText = game.score;
    }

    onMounted(() => {
      titleShow();
    });


    return {
      title,
      subTitle,
      count
    };
  }
}
</script>
    
<style lang="scss" scoped>
@import "@/assets/scss/__variables.scss";
$bg-width: 50px;
$bg-height: 50px;

#work {
  .inner {
    display: block;
    .work_top_content{
      background: url(data:image/png;base64,) repeat 0 0;
      -webkit-animation: bg-scrolling-reverse 0.92s infinite;
      -moz-animation: bg-scrolling-reverse 0.92s infinite;
      -o-animation: bg-scrolling-reverse 0.92s infinite;
      animation: bg-scrolling-reverse 0.92s infinite;
      -webkit-animation-timing-function: linear;
      -moz-animation-timing-function: linear;
      -o-animation-timing-function: linear;
      animation-timing-function: linear;

      .work_text_box{
        padding: vw(400px 0);
        display: flex;
        justify-content: space-between;
        align-items: center;
      }
      
      h2 {
        width: 45%;
        font-size: vw(80px);
        color: #141414;
        font-weight: bold;
        letter-spacing: vw(-3px);
        line-height: 1.3;
        font-family: 'Noto Sans Korean';
      }

      h3 {
        width: 55%;
        font-size: vw(80px);
        color: #141414;
        font-weight: bold;
        letter-spacing: vw(-3px);
        line-height: 1.3;
        font-family: 'Noto Sans Korean';
        .count{
          display: inline-block;
          text-align: center;
          font-size: vw(80px);
          width: vw(100px);
          color: $dpColor;
        }
      }

      @keyframes         bg-scrolling-reverse {
          100% { background-position: $bg-width $bg-height; }
        }

    }
 
  }
}


@media (max-width: 960px) {
  #work {
    .inner{
      .work_top_content{
        .work_text_box{
          padding:vw(200px 0 150px);
          flex-direction:column;
        }
        h2,h3{
          width: 100%;
          font-size:vw(100px);
        }
        h2{
          br{ display: none; }
        }
        h3{
          .count{ font-size:vw(100px)}
        }
      }
    }
  
  }
}


</style>
    